body {
  background: #2b3e44;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: #89c037;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

button {
  background: #1a6f6b;
  border-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  color: white;
  display: block;
  width: 100%;
  border-radius: 4px / 3px;
  line-height: 23px;
  margin: 3px 0;
  padding: 0 26px;
}

.name {
  color: #89c037;
  text-align: center;
}


